*,
*::after,
*::before {
  font-family: 'Montserrat', sans-serif;

  line-height: normal;
  font-style: normal;

}

*,
*::after,
*::before {
  box-sizing: border-box;
}

a {
  text-decoration: none;

}

html,
body {
  height: 100%;
}

.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1 1 auto;
}

.container {
  max-width: 1450px;
  margin: 0 auto;
}